<template lang="html">
  <div class="orderEdit">
    <el-breadcrumb separator="/" class="app-breadcrumb">
      <el-breadcrumb-item :to="{ name: 'OrderList' }">订单列表</el-breadcrumb-item>
      <el-breadcrumb-item>修改订单</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :rules="rules" :model="formData" label-width="100px" class="app-form">
      <el-form-item label="产品名称：" prop="productName">
        <!-- bug: https://github.com/ElemeFE/element/pull/15565 >>> reset.scss -->
        <el-input v-model="formData.productName" placeholder="请输入产品名称" type="text" minlength="2" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="员工姓名：" prop="staffName">
        <el-input v-model="formData.staffName" placeholder="请输入员工姓名" type="text" minlength="2" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="员工手机：" prop="staffPhone">
        <el-input v-model="formData.staffPhone" placeholder="请输入员工手机" type="number"></el-input>
      </el-form-item>
      <el-form-item label="客户姓名：" prop="customerName">
        <el-input v-model="formData.customerName" placeholder="请输入客户姓名" type="text" minlength="2" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="客户手机：" prop="customerPhone">
        <el-input v-model="formData.customerPhone" placeholder="请输入员工手机" type="number"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">立即修改</el-button>
        <el-button @click="toOrderList">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
.orderEdit {
  padding: 0 40px;

  .app {
    &-breadcrumb {
      margin-bottom: 40px;
    }

    &-form {
      width: 600px;
    }
  }
}
</style>

<script>
import { mapState, mapActions } from 'vuex'

export default {

  name: 'OrderEdit',
  data () {
    return {
      formData: {},
      rules: {
        productName: [
          { required: true, message: '请输入产品名称', trigger: 'blur' },
          {
            min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur',
          },
        ],
        staffName: [
          { required: true, message: '请输入员工姓名', trigger: 'blur' },
          {
            min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur',
          },
        ],
        staffPhone: [
          { required: true, message: '请输入员工手机', trigger: 'blur' },
          {
            min: 11, max: 11, message: '长度为 11 个数字', trigger: 'blur',
          },
        ],
        customerName: [
          { required: true, message: '请输入客户姓名', trigger: 'blur' },
          {
            min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur',
          },
        ],
        customerPhone: [
          { required: true, message: '请输入客户手机', trigger: 'blur' },
          {
            min: 11, max: 11, message: '长度为 11 个数字', trigger: 'blur',
          },
        ],
      },
    }
  },
  computed: {
    ...mapState(['orderDetail']),
  },
  watch: {
    orderDetail (value) {
      this.formData = {
        ...value, // 直接等于 value 会报错，必须解构赋值
      }
    },
  },
  methods: {
    ...mapActions(['getOrderDetail']),
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            message: '创建成功！',
            type: 'success',
          })

          return true
        }

        return false
      })
    },
    resetForm (formName) {
      console.log(formName)

      this.$refs[formName].resetFields()
    },
    toOrderList () {
      this.$router.push({
        name: 'OrderList',
      })
    },
  },
  created () {
    const { query } = this.$route
    const { orderId } = query

    if (orderId) {
      this.getOrderDetail({
        orderId,
      })
    }
  },

}
</script>
